import { React, useEffect, useState } from 'react'
import styled from 'styled-components'
import { AutoColumn } from '../Column'
import NumericInput from '../NumericInput'
import { RowBetween } from '../Row'
import { TokenListProvider } from '@solana/spl-token-registry';
import { Dropdown, DropdownButton } from 'react-bootstrap'
import { getChainId } from '../../utils/mics'
import { network } from '../../utils/constants'
import {
    getTokenBalance,
    getTokenAccounts,
    getAssociatedTokenAddress,
} from '../../utils/api'

const InputPanel = styled.div`
  position: relative;
  border-radius: 1.25rem;
  background-color: white;
  z-index: 1;
  width: 100%;
`

const ContainerRow = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1.25rem;
  border: 1px solid rgb(240, 240, 255);
  transition: border-color 300ms step-start,
    color 500ms step-start;
  background-color: white;
`

const InputContainer = styled.div`
  flex: 1;
  padding: 1rem;
`

const LabelRow = styled.div`
  display: flex;
  flex-flow: row-reverse wrap;
  align-items: center;
  span:hover {
    cursor: pointer;
    color: gray;
  }
`

export default function AmountInputPanel({
    connection,
    account,
    value,
    onChangeAmount,
    onSelectToken,
    onTokenBalance,
    onSetShowCreateAccount,
    onError,
    updateBalance,
    updatedAddress,
    setBalanceUpdated,
}) {
    const [allTokens, setAllTokens] = useState([
        {
            decimals: 9,
            symbol: "MetaSPL",
            address: "Cv7DvSt9842MSxX8LXQewpfcWNC8qT5P9w7wpHshcEzx",
        }
    ]);

    const [selectedCurrencyBalance, setSelectedCurrencyBalance] = useState("");
    const [selectedTokenSymbol, setSelectedTokenSymbol] = useState("Select a token");

    const handleSelectToken = async (event) => {
        const data = allTokens.filter((element) => {
            return element.address === event
        });
        const tokenInfo = data[0];

        onSelectToken(data[0]);
        setSelectedTokenSymbol(data[0].symbol);

        if (account) {
            const addresses = await getTokenAccounts(
                connection, tokenInfo.address, account
            );

            if (addresses && addresses.length > 0) {
                const tokenAccount = await getAssociatedTokenAddress(
                    tokenInfo.address, account
                );

                const balance = await getTokenBalance(
                    connection, tokenAccount
                );

                setSelectedCurrencyBalance(balance.value.uiAmount);
                onTokenBalance(balance.value.uiAmount);
            } else {
                onSetShowCreateAccount();
                onError("You don't have token account for this token.")
            }
        }
    }

    const fetchTokenList = () => {
        const chainId = getChainId(network);
        new TokenListProvider().resolve().then(async (tokens) => {
            const tokenList = tokens.filterByChainId(chainId).getList();
            setAllTokens(allTokens.concat(tokenList));
        });
    }

    const onClickMax = () => {
        onChangeAmount(selectedCurrencyBalance);
    }

    useEffect(() => {
        if (allTokens.length === 0) {
            fetchTokenList();
        }

        if (updateBalance) {
            handleSelectToken(updatedAddress);
            setBalanceUpdated();
        }
        // eslint-disable-next-line
    }, [updateBalance]);

    return (
        <InputPanel>
            <ContainerRow>
                <InputContainer>
                    <AutoColumn gap="md">
                        <LabelRow>
                            <RowBetween>
                                <div
                                    fontWeight={500}
                                    fontSize={14}
                                >
                                    Amount
                                </div>

                                {account && (
                                    <div
                                        onClick={onClickMax}
                                        color="rgb(86, 90, 105)"
                                        fontWeight={500}
                                        fontSize={14}
                                        style={{ display: 'inline', cursor: 'pointer' }}
                                    >
                                        {selectedCurrencyBalance !== ""
                                            ? 'Balance: ' + selectedCurrencyBalance.toFixed(3)
                                            : ' -'
                                        }
                                    </div>
                                )}
                            </RowBetween>
                        </LabelRow>

                        <LabelRow>
                            <RowBetween>
                                <NumericInput
                                    value={value}
                                    onChange={onChangeAmount}
                                />

                                <DropdownButton
                                    title={selectedTokenSymbol}
                                    onSelect={(e) => handleSelectToken(e)}
                                >
                                    {allTokens.map((element, key) => (
                                        element.symbol !== "SOL" ? (
                                            <Dropdown.Item
                                                key={key}
                                                eventKey={element.address}
                                            >
                                                {element.symbol}
                                            </Dropdown.Item>
                                        ) : null
                                    ))}
                                </DropdownButton>
                            </RowBetween>
                        </LabelRow>
                    </AutoColumn>
                </InputContainer>
            </ContainerRow>
        </InputPanel>
    )
}