import axios from 'axios';
import {
    PublicKey,
    Transaction,
    TransactionInstruction,
} from '@solana/web3.js';
import ReactGA from 'react-ga';
import BigNumber from 'bignumber.js';
import {
    relayerUrl,
    FEE_PAYER_PUBLIC_KEY,
    METASOL_PROGRAM_ID,
    submitTxLayout,
    TOKEN_PROGRAM_ID,
    METASOL_PUBLIC_KEY,
} from "./constants";
import { u64 } from './uint64';
import { getAssociatedTokenAddress } from './api';

export const getChainId = (network) => {
    let chainId = 0;

    switch (network) {
        case "mainnet-beta":
            chainId = 101;
            break;
        case "testnet":
            chainId = 102;
            break;
        case "devnet":
            chainId = 103;
            break;
        default:
            chainId = 102;
            break;
    }

    return chainId;
}

export const signAndSendTransaction = (
    connection,
    solletWallet,
    recipient,
    transferAmount,
    selectedTokenData,
) => {
    return new Promise(async (resolve, reject) => {
        try {
            const senderSplAccount = await getAssociatedTokenAddress(
                selectedTokenData.address, solletWallet.publicKey.toString(),
            );
            const recipientSplAccount = await getAssociatedTokenAddress(
                selectedTokenData.address, recipient,
            );
            const feePayerSplAccount = await getAssociatedTokenAddress(
                selectedTokenData.address, FEE_PAYER_PUBLIC_KEY.toString(),
            );

            const keys = [
                { pubkey: senderSplAccount, isSigner: false, isWritable: true },
                { pubkey: recipientSplAccount, isSigner: false, isWritable: true },
                { pubkey: TOKEN_PROGRAM_ID, isSigner: false, isWritable: false },
                { pubkey: new PublicKey(selectedTokenData.address), isSigner: false, isWritable: false },
                { pubkey: solletWallet.publicKey, isSigner: true, isWritable: true },
                { pubkey: feePayerSplAccount, isSigner: false, isWritable: true },
                { pubkey: METASOL_PUBLIC_KEY, isSigner: false, isWritable: true },
            ];

            const data = Buffer.alloc(submitTxLayout.span);

            const amount = Number(
                new BigNumber(transferAmount).times(
                    new BigNumber(10).exponentiatedBy(
                        new BigNumber(selectedTokenData.decimals)
                    )
                )
            );

            submitTxLayout.encode(
                {
                    instruction: 1,
                    amount: new u64(amount).toBuffer(),
                },
                data,
            );

            const instruction = new TransactionInstruction({
                keys,
                programId: METASOL_PROGRAM_ID,
                data,
            });

            const recentBlock = await connection.getRecentBlockhash();

            const transaction = new Transaction({
                feePayer: FEE_PAYER_PUBLIC_KEY,
                recentBlockhash: recentBlock.blockhash
            }).add(instruction);

            const signedTx = await solletWallet.signTransaction(transaction);

            const bufferTx = signedTx.serialize({
                verifySignatures: false
            });

            const result = await sendToRelayer(bufferTx);
            resolve(result);

            ReactGA.event({
                category: 'Transfer',
                action: 'Executed',
                label: amount.toString(),
            });
        } catch (error) {
            reject(error);
        }
    });
}

export const createAndSendTestTokens = (
    mintAddress,
    ownerAddress,
) => {
    return new Promise((resolve, reject) => {
        axios.post
            (
                `${relayerUrl}/create-wallet`,
                {
                    mintAddress,
                    ownerAddress,
                }
            )
            .then((result) => {
                resolve(result)

                ReactGA.event({
                    category: 'Wallet',
                    action: 'Created',
                });
            })
            .catch((error) => {
                reject(error);
            })
    });
};


const sendToRelayer = (bufferTx) => {
    return new Promise((resolve, reject) => {
        axios.post
            (
                `${relayerUrl}/execute-tx`,
                {
                    bufferTx
                }
            )
            .then((txSignature) => {
                resolve(txSignature)
            })
            .catch((error) => {
                reject(error);
            })
    });
};
